html {
  box-sizing: border-box;
}

*, ::after, ::before {
  box-sizing: inherit
};

#root {
  font-family: "Playfair Display";
  background-image: url("bilon.jpg");
  background-position: center;
  background-size: contain;
  background-repeat: repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}